import React, { Component } from 'react';

import './App.css';


import {BrowserRouter as Router,Route,Link,withRouter} from 'react-router-dom';


import routers from './model/router.js';

import { Layout, Menu, Anchor, Icon, Popover,Button } from 'antd';

import wechat_img from './imgs/wechat.jpg';


const { Header, Footer, Content } = Layout;

const setTitle = title => () => document.title = title;


class App extends Component {
  constructor(props){
      super(props);
      this.state = {
          List:[
              {
                  aid:'0',
                  title:'首页'
              },
              {
                  aid:'1',
                  title:'装机必备'
              },
              {
                  aid:'2',
                  title:'生活工作'
              },
              {
                  aid:'3',
                  title:'少数派新手包'
              },
              {
                  aid:'4',
                  title:'全部'
              }
          ],
          redirectToReferrer: false
      };
  }

  render(){

    const {List} = this.state;
    var name = window.location.pathname;
    var defaultSelectedKey = '';
    // console.log(this.props.location.pathname)

    // this.componentDidUpdate
    // eslint-disable-next-line default-case
    switch (name){
      case '/':
        defaultSelectedKey = '0';
        break;
      case '/class/1':
        defaultSelectedKey = '1';
        break;
      case '/class/2':
        defaultSelectedKey = '2';
        break;
      case '/class/3':
        defaultSelectedKey = '3';
        break;
      case '/class/4':
        defaultSelectedKey = '4';
        break;
    }

    return (

      <Router>
        <div className="App">
        <Layout className="layout">
          <Anchor>
            
            <Header>
              <div className="logo"/>
              <Menu
                theme='light'
                mode="horizontal"
                defaultSelectedKeys={[defaultSelectedKey]}
                style={{ lineHeight: '70px',width:'80%' }}
              >

                {
                  
                  List.map((value,key)=>{
                    if(key===0)
                      return <Menu.Item key={key}><Link to="/index" onClick={setTitle('少数派 - 高效工作，品质生活')}>{value.title} </Link></Menu.Item>
                    else
                      return <Menu.Item key={key}><Link to={`/class/${value.aid}`} onClick={setTitle('全部商品 - 少数派 - 少数派')}>{value.title} </Link></Menu.Item>   
                  })
                }




                {/* <Menu.Item key="1"><Link to="/">首页</Link></Menu.Item>
                <Menu.Item key="2"><Link to='/class'>装机必备</Link></Menu.Item>
                <Menu.Item key="3"><Link to="/class">生活工作</Link></Menu.Item>
                <Menu.Item key="4"><Link to="/class">少数派新手包</Link></Menu.Item>
                <Menu.Item key="5"><Link to="/class">全部</Link></Menu.Item> */}
              </Menu>
              <div style={{width:'20%'}}>
                <Button style={{marginTop:'16px'}} type="primary" shape="round" size={'large'}  onClick={this.login}>登陆</Button>
              </div>


            </Header>
          </Anchor>

          <Content style={{ padding: '50px 50px 0px 50px' }}>
            <div  className='MainView' style={{ background: '#fff', padding: 24, minHeight: 280 }}>
                {
                  routers.map((route,key)=>{
                    if(route.exact){
                      return <Route key={key} exact path={route.path} component={route.component}/>
                    }else{
                      return <Route key={key} path={route.path} component={route.component}/>
                    }
                  })
                }
            </div>
          </Content>

          <Footer style={{ textAlign: 'center',color:'#999' }}>
            <div className='share'>
              <a href='mailto:contact@sspai.com'  target="_blank">
                <Icon type="mail" theme="filled" />
              </a>
              <a href='https://www.zhihu.com/org/shao-shu-pai-46/activities' target="_blank">
                <Icon type="zhihu-circle" theme="filled" />
              </a>
              <a href="http://weibo.com/sspaime" target="_blank">
                <Icon type="weibo-circle" theme="filled" />
              </a>
                  <Popover
                      placement='right'
                      content={
                            <img src={wechat_img} style={{width:'100%'}}></img>
                            
  
                      }>
                      <a className='i-wechat'>
                        <Icon type="wechat" theme="filled" />
                      </a>
                  </Popover>
              <a href="https://twitter.com/sspai_com" target="_blank">
                <Icon type="twitter-circle" theme="filled" />
              </a>

            </div>
            <ul className='link'>
              <li>支持我们</li>
              <li>作者招募</li>
              <li>用户协议</li>
              <li>FAQ</li>
              <li>Contact Us</li>
            </ul>
            <div className='copyright'>
            © 2013-2019 
            <a href="https://sspai.com/" target="_blank">少数派</a> 
            | 
            <a href="http://www.miitbeian.gov.cn/" target="_blank">粤ICP备09128966号-4</a>   
            | CC BY-NC 4.0
            </div>
            
          </Footer>
        </Layout>
        </div>
      </Router>

  );
  }
  
}

export default withRouter(App);
// export default App;
