import React, { Component } from 'react';

import {Breadcrumb, Icon} from 'antd';
import {Link} from 'react-router-dom';

class Classfy extends Component{
    constructor(props){
        super(props);
        this.state = {
            List1:[
                {
                    id: 4,
                    name: "全部" 
                }, 
                { 
                    id: 1,
                    name: "装机必备" 
                }, 
                { 
                    id: 2,
                    name: "生活工作" 
                }, 
                { 
                    id: 3,
                    name: "少数派新手包" 
                }
            ],
            List2:[
                {
                    id: 1,
                    name: "全部" 
                }, 
                { 
                    id: 2,
                    name: "系统优化" 
                }, 
                { 
                    id: 3,
                    name: "影音媒体" 
                }, 
                { 
                    id: 4,
                    name: "效率日程" 
                }, 
                { 
                    id: 5,
                    name: "写作编辑" 
                }, 
                { 
                    id: 6,
                    name: "阅读批注" 
                }, 
                { 
                    id: 7,
                    name: "学术教育" 
                }, 
                { 
                    id: 8,
                    name: "创意工作" 
                }
            ],
            List3:[
                {
                    id: 1,
                    name: "不限" 
                }, 
                { 
                    id: 2,
                    name: "macOS" 
                }, 
                { 
                    id: 3,
                    name: "Windows" 
                }, 
                { 
                    id: 4,
                    name: "双平台" 
                }
            ],
            l1clicked:1,
            l2clicked:1,
            l3clicked:1
        }
    }
    
    target(id,type) {
        switch (type) {
            case 1:
                this.setState({l1clicked:id});
                break;
            case 2:
                this.setState({l2clicked:id});
                break;
            case 3:
                this.setState({l3clicked:id});
                break;
            default:
                break;
        }
    }

    render(){
        let { List1, List2, List3, l1clicked, l2clicked, l3clicked } = this.state;
        
        const classaid= parseInt(this.props.match.params.aid);

        l1clicked = classaid;
        // console.log(classaid)
        // console.log(l1clicked,l2clicked,l3clicked)
        return (
            <div>
                <div className='mall-all-breadcrumb'>
                    <Breadcrumb separator={'>'}>
                        <Breadcrumb.Item href="/">
                            <Icon type="home" />
                            <span>首页</span>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item href="/class/4">
                            <Icon type="appstore" />
                            <span>全部</span>
                        </Breadcrumb.Item>
                        {
                            ShowClass(classaid)
                        }
                    </Breadcrumb>
                </div>

                <div className='el-row'>
                    <div className='el-col'>分类:</div>
                    <div style={{width:'91.66667%'}}>
                            {
                                List1.map((value,key)=>{
                                    return (
                                        <div
                                            key={key}
                                            className={l1clicked === value.id ? "item itemIsActive" : "item"}
                                            onClick={this.target.bind(this, value.id,1)}
                                        >
                                            
                                            <Link to={key > 0 ? `/class/${key}`:'/class/4'}>{value.name}</Link>
                                        </div>
                                    )
                                    
                                })
                            }
                    </div>

                </div>
                <div className='el-row'>
                    <div className='el-col'>类型:</div>
                    <div style={{width:'91.66667%'}}>
                        {
                            List2.map((value,key)=>{
                                return (
                                    <div
                                        key={key}
                                        className={l2clicked === value.id ? "item itemIsActive" : "item"}
                                        onClick={this.target.bind(this, value.id,2)}
                                    >
                                    <Link to={`/class/${classaid}`}>{value.name}</Link>
                                    </div>
                                )
                                
                            })
                        }
                           
                    </div>

                </div>
                <div className='el-row'>
                    <div className='el-col'>平台:</div>
                    <div style={{width:'91.66667%'}}>
                        {
                            List3.map((value,key)=>{
                                return (
                                    <div
                                        key={key}
                                        className={l3clicked === value.id ? "item itemIsActive" : "item"}
                                        onClick={this.target.bind(this, value.id,3)}
                                        // onClick={this.props.upgradeFather}

                                    >
                                    <Link to={`/class/${classaid}`}>{value.name}</Link>
                                    </div>
                                )
                                
                            })
                        }
                    </div>

                </div>


            </div>
        );
    };
}


function ShowClass(int) {
    switch (int) {
        case 1:
            return (
                <Breadcrumb.Item href="/class/1">
                    <span>装机必备</span>
                </Breadcrumb.Item>
            )
        case 2:
            return (
                <Breadcrumb.Item href="/class/2">
                <span>生活工作</span>
                </Breadcrumb.Item>
            )
        case 3:
            return (
                <Breadcrumb.Item href="/class/3">
                <span>少数派新手包</span>
                </Breadcrumb.Item>
            )
        default:
            break;
    }
}


export default Classfy;