import Home from '../components/Home';
import Classfy from '../components/Classfy';
import Content from '../components/Content';
import Item from '../components/Item';

let routers = [
    {
        path: "/",
        component:Home,
        exact:true
    },
    {
        path: "/index",
        component:Home,
        exact:true
    },
    {
        path:"/class/:aid",
        component:Classfy
    },
    {
        path:"/class/:aid",
        component:Content
    },
    {
        path:"/item",
        component:Item
    }
];

export default routers;